.container::-webkit-scrollbar{
    display: none !important;
    width: 0;
    visibility: hidden;
}
div::-webkit-scrollbar{
    display: none;
}
.input{
    border: none;
    border-bottom: 1px solid #efefef;
   
    width: 100%;
    height: 5vh;
    line-height: 5em;
    font-size: 13px;
    

    
}

.input:focus{
        outline: none;
    }

.container{
    overflow-x: hidden;
    overflow-y: auto;  
    font-family: 'Raleway', sans-serif;
   
}
.exterior{display: block;
     height:auto;
     padding: 10px;
     padding-top: 0px;
     margin-top:0px;
     border-bottom:1px solid #EFEFEF;
     background:"white"
}
.cover{
    display: inline-block;
    margin-right:30px;
     width: 20%;
}
.image{
    border-radius: 50%; 
    border:0.5px solid #EFEFEF;
    width: 50;
    height: 50;
    transform:scale(2.5) translate(50%,0);
     margin-bottom: 30%;
}
.text{
    display: inline-block;
    padding-top:30px;
    margin-left:30px;
    width:50%;
}
.covertwo{
    display: inline-block;
    transform: translateY(-18vh);

}
.button{
    border: none; 
    background-color:transparent;
    font-size: 20px;
    font-weight: bold;
    color: black;
}
.coverthree{
    padding-left:30%;
    height:auto;
    max-height:30vh;
    margin-top:10px; 
}




