.container3{
    height: auto;
    background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
    
}
.footer{
    display: flex;
   height:auto;
   overflow-y: hidden;
    
   
}
.wave{
    transform: translateY(-10%);
}

.logo{
    margin:0;
    padding: 0;
    height: 80%;
    display: flex;
    justify-content:center;
    align-items: center;
    height:30vh}
.logo img{
   transform: scale(.75);
}
.contact{
    display: flex;
    position: absolute;
    right:0;
    height: 30vh;
    justify-content:center;
    align-items:center;
    
  
    
}
.icons{
    color: white;
    transform: scale(3);
    margin : 25px;
}

@media(max-width:768px) {
   .footer{
        display: block;
        max-height: 100vh;
    }
    .logo{
       
        height: auto;
        display: flex;
        justify-content:center;
        align-items: center;
        max-height: 30vh;
    }
    .contact{
        display: flex;
        position: relative;
      
       
        justify-content:center;
        align-items:center;
        
      
        
    }
    .icons{
       
        
       
    }
    
}
