

*{
    box-sizing: border-box;
    
}
.wrapper{
    padding: 0;
    margin:0px;
    
    min-height: 100vh;
    max-height: auto;
    height: auto;
    transform:translateY(-30px);

    
    font-family: 'Lobster', cursive;
    font-size: 20px;
    font-weight: bolder;
    
    
    
}

.container{
    min-height: 80vh;
    width: 95%;
    margin-left: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Lobster', cursive;
}
    

.header{
    margin:0 0 0 0;
    padding: 0;
    width: auto;
    /* background-color: #f3ec78;
    background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    margin-top: 30px;
    margin-bottom: 10px;
    color:black; */
    text-align: center;
}

.timeline{
    padding:0;
   margin: 0;
    width: 80%;
    height: auto;
    max-width: 800px;
    position: relative;
}

.description{
    font-family: 'Zen Loop', cursive;
    font-size: 30px;
    
    color: #525C58;
}
.timeline ul {
    padding: 0;
    
    margin:0;
    list-style: none;
   
}

.timeline .job{
    padding:0 10px;
    color: #505050;
    border-radius: 10px; 
    margin-bottom: 30px;
    cursor: pointer;
    
}

.timeline ul li:last-child{
    margin-bottom: 0;
}

.timeline-content h1{
    font-size: 25px;
    font-weight: 500;
    line-height: 20px;}

.timeline-content h3{
    margin: 2px 0;
    font-size: 20px;
    font-weight: 500;
    line-height: 20px;}
    

.timeline-content p{
    font-size: 15px;
    line-height: 30px;
    font-weight: 300;
}

.timeline-content .date{
    font-size: 12px;
    font-weight: 300;
    right: 0;
    margin: 10px 0;

}
.description{
    height: 0;
    display: none;
    transition: height ease-in-out 2s;
    
    
}
.description li{
    padding: 0;
    font-size: 17px;
    margin: 5px 2px;
   background: transparent;
    list-style: none;
}

.timeline ul li:hover .description{
    display: block; 
    height: auto;
    }

 @media(min-width:768px) {
    .timeline::before{
        content: " ";
        position: absolute;
        height: 100%;
        width: 2px;
        left: 50%;
        transform: translateX(-50%);
        background-color: rgba(225, 225, 225, 0.3) ;
        
    }
    .timeline .job{
        width: 50%;
        margin-bottom: 30px;
        position: relative;
        
       
    }
    .timeline .job:nth-child(odd){
        float: left;
        clear: right;
        transform: translateX(-30px);
        
    
        
    }
    .timeline .job:nth-child(even){
        float: right;
        clear: left;
        transform: translateX(30px);
    }

    .timeline .job::after{
        content: ' ';
        position: absolute;
        height: 15px;
        width: 15px;
        top: 0;
        background-color: rgba(225, 225, 225, 0.7) ;
        
    }

    .timeline .job:nth-child(odd)::after{
      right: 0; 
      transform-origin: center;
      transform: translate(37.5px,-50%) rotate(45deg) ;
    }
    .timeline .job:nth-child(even)::after{
       left: 0;
       transform-origin: center;
       transform:  translate(-37.5px,-50%) rotate(45deg) ;
    }

    .timeline-content .date{
        font-size: 12px;
        font-weight: 300;
        right: 0;
        margin: 10px 0;

    }

    
    


    
} 
 
